	let reg_full = /[a-z0-9.]+@[a-z0-9]+\.[a-z]+\.([a-z]+)|\w+@\w+.([a-z]{1,3})|#(?![0-9_]+\b)([a-zA-Z0-9_]{1,30})(\b|\r)/g
	let found = text.match(reg_full)
