			} else
			if (found[i].includes('@')) {
			 	map_matches[found[i]] = '<span class="email">' + found[i] +  '</span>'
			}
	  }
		return text.replace(reg_full,function(matched: any) {
			return map_matches[matched];
		})
	} else //if (found) 
